import React, { useState } from "react";

import Grid from '@material-ui/core/Grid';

import Button from '../../../theme/components/Button';
import Modal from '../../../theme/components/Modal';
import { getDisplayBalance } from '../../../utils/formatBalance';
import { IModalProps } from "../../../utils/interface";
import { StrategiesStaticInfo } from "../../../protocol/config";
import useCloseStrategy from "../../../hooks/callbacks/strategies/useCloseStrategy";
import DataField from "../../../theme/components/DataField";
import theme from "../../../theme";
import useIsStrategyApproved from "../../../hooks/state/strategies/useIsStrategyApproved";
import useApproveStrategy from "../../../hooks/callbacks/strategies/useApproveStrategy";
import TextWrapper from "../../../theme/components/TextWrapper";

interface IProps {
  trove: any;
  strategy: StrategiesStaticInfo;
}

const ConfirmCloseModal = (props: IModalProps & IProps) => {
  const { onClose, open, strategy, trove } = props;

  const [closingStrategy, setClosingStartegy] = useState<boolean>(false);
  const closeStrategy = useCloseStrategy(strategy);

  const OnCloseStrategy = () => {
    setClosingStartegy(true)
    closeStrategy(
      () => {
        window.location.href = '/#/strategies/all-strategies';
        setClosingStartegy(false)
      },
      () => {
        setClosingStartegy(false);
      }
    )
  }

  const [isApprovingStrategy, setIsApprovingStrategy] = useState<boolean>(false);
  const strategyApprovalStatus = useIsStrategyApproved(props.strategy);
  const approveStrategy = useApproveStrategy(props.strategy)
  const onApproveStrategy = () => approveStrategy(
    () => setIsApprovingStrategy(false),
    () => { },
  );

  return (
    <Modal
      title={'Confirm Close Loan'}
      open={open}
      handleClose={onClose}
      closeButton
    >
      <div>
        <div className={'m-b-32'}>
          <div className={'single-line-start-between m-b-8'}>
            {/* <TextWrapper
              text={'You will receive'}
              fontWeight={600}
              Fcolor={theme.color.transparent[200]}
            />
            <div>
              {strategy.borrowTokens.map((item) => {
                return (
                  <TextWrapper
                    text={1000 + ` ${item}`}
                    fontWeight={600}
                    Fcolor={theme.color.transparent[100]}
                    className={'m-b-4'}
                    align={'right'}
                  />
                )
              })}
            </div> */}
          </div>
          {/*<DataField
            label={'Collateral Ratio'}
            value={'123'}
            labelFontColor={theme.color.transparent[100]}
            labelFontWeight={'bold'}
            className={'m-b-16'}
          />
          <DataField
            label={'Collateral Ratio'}
            value={'123'}
            labelFontColor={theme.color.transparent[100]}
            labelFontWeight={'bold'}
            className={'m-b-16'}
          />
          <DataField
            label={'Collateral Ratio'}
            value={'123'}
            labelFontColor={theme.color.transparent[100]}
            labelFontWeight={'bold'}
            className={'m-b-16'}
          />*/}
        </div>
        <Grid container spacing={2} direction={"row"}>
          <Grid item sm={12} xs={12}>
            <Button
              variant={"default"}
              onClick={() => {
                setIsApprovingStrategy(true);
                onApproveStrategy();
              }}
              disabled={strategyApprovalStatus.isApproved || strategyApprovalStatus.isLoading || isApprovingStrategy}
              loading={isApprovingStrategy}
            >
              {strategyApprovalStatus.isApproved ? 'Strategy Approved' : 'Approve Strategy'}
            </Button>
          </Grid>
        </Grid>
        <Grid container spacing={2}>
          <Grid item lg={6} md={6} sm={12} xs={12}>
            <Button
              variant={'transparent'}
              onClick={() => onClose()}
              tracking_id="close_loan"
              tracking_params={{
                collateral: strategy.trove,
                action: 'cancel',
                amount: Number(getDisplayBalance(trove.coll, 18, 3)),
                other: {
                  debt: Number(getDisplayBalance(trove.debt, 18, 3)),
                },
              }}
            >
              Cancel
            </Button>
          </Grid>
          <Grid item lg={6} md={6} sm={12} xs={12}>
            <Button
              disabled={!strategyApprovalStatus.isApproved || strategyApprovalStatus.isLoading}
              loading={closingStrategy}
              onClick={OnCloseStrategy}
              tracking_id="close_loan"
              tracking_params={{
                collateral: strategy.trove,
                action: 'confirm',
                amount: Number(getDisplayBalance(trove.coll, 18, 3)),
                other: {
                  debt: Number(getDisplayBalance(trove.debt, 18, 3)),
                },
              }}
            >
              Confirm Close Loan
            </Button>
          </Grid>
        </Grid>
      </div>
    </Modal>
  );
};

export default ConfirmCloseModal;
