import { BigNumber } from '@ethersproject/bignumber';
import { useCallback, useEffect, useMemo, useState } from 'react';
import { useWallet } from 'use-wallet';

import { FarmingConfigurations, StrategiesStaticInfo } from '../../../protocol/config';
import { useBlockNumber } from '../../../state/application/hooks';
import { useGetActiveChainId } from '../../../state/chains/hooks';
import useCore from '../../useCore';

type State = {
  isLoading: boolean;
  value: [BigNumber, BigNumber];
};

export default (strategy: StrategiesStaticInfo, collat: BigNumber) => {
  const [value, setValue] = useState<State>({
    isLoading: true,
    value: [BigNumber.from(0), BigNumber.from(0)],
  });

  const core = useCore();
  const { account } = useWallet();
  const chainId = useGetActiveChainId();

  const fetchValue = useCallback(async () => {
    if (!account) {
      setValue({ isLoading: false, value: [BigNumber.from(0), BigNumber.from(0)] });
      return;
    }

    const contract = core.contracts(strategy.contract, chainId);
    const value: [BigNumber, BigNumber] = await contract.underlyingCollateralFromBalance(
      collat,
    );

    setValue({ isLoading: false, value });
  }, [account, core, strategy.contract, chainId, collat]);

  useEffect(() => {
    if (core.isUnlocked) {
      fetchValue().catch((err) => console.error(`Failed to fetch strategy exposure: ${err}`));
    }
  }, [core.isUnlocked, account, fetchValue]);

  return value;
};
