import { BigNumber } from 'ethers';
import React, { useEffect, useMemo, useState } from 'react';
import styled from 'styled-components';

import '../../../App.css';
import { StrategiesStaticInfo } from '../../../protocol/config';

import Step1 from './Step1';
import Step2 from './Step2';
import Step3 from './Step3';
import useGetAPRValue from "../../../hooks/state/strategies/useGetAPRValue";
import useHasStakingBalance from "../../../hooks/state/strategies/useHasStakingBalance";

interface IProps {
  strategy: StrategiesStaticInfo;
}

export interface ITokenObj { [name: string]: string }

export const minSlider = 1;

const CreatePosition = (props: IProps) => {
  const { strategy } = props;

  const [cr, setCR] = useState<string>('');
  const [activeStep, setActiveStep] = useState<number>(0);
  const [sliderValue, setSliderValue] = useState(minSlider);
  const [completed, setCompleted] = useState(new Set());
  const [tokenSupplyObj, setTokenSupplyObj] = useState<{ [name: string]: string }>({});
  const [tokenBorrowObj, setTokenBorrowObj] = useState<{ [name: string]: string }>({});
  const aprValues = useGetAPRValue(strategy.trove);
  const hasStaked = useHasStakingBalance();

  useEffect(() => {
    strategy.depositTokens.forEach((t) => {
      const supply = tokenSupplyObj;
      supply[t] = '';
      const borrow = tokenBorrowObj;
      borrow[t] = '';
      setTokenSupplyObj(supply);
      setTokenBorrowObj(borrow);
    })
    resetData();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [strategy]);

  useEffect(() => {
    const principal = Number(tokenSupplyObj.BUSD || tokenSupplyObj.USDC) / 2

    strategy.borrowTokens.forEach((t) => {
      const borrow = tokenBorrowObj;
      const multiplier = strategy.leverageMultiplier ? strategy.leverageMultiplier[t] : 1;
      borrow[t] = String(principal * (sliderValue) * multiplier);
      setTokenBorrowObj({ ...borrow });
    })
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [sliderValue, strategy.depositTokens, tokenSupplyObj]);

  const mcr = useMemo(() => {
    if (sliderValue === 1) return 0;
    return (sliderValue * 100) / (sliderValue - 1);
  }, [sliderValue])

  const resetData = () => {
    setActiveStep(0);
    setSliderValue(minSlider);
    setCompleted(new Set());
  };

  function getSteps() {
    return ['Supply Collateral', 'Choose Leverage', 'Confirm'];
  }

  function getStepContent(step: number) {
    switch (step) {
      case 0:
        return step1();
      case 1:
        return step2();
      case 2:
        return step3();
      default:
        return step1(); // Maybe show a error/feedback popup.
    }
  }

  const step1 = () => {
    return (
      <Step1
        strategy={strategy}
        tokenSupplyObj={tokenSupplyObj}
        setTokenSupplyObj={(key, value) => {
          const temp = tokenSupplyObj;
          temp[key] = value;
          setTokenSupplyObj({ ...temp });
        }}
        onClose={() => window.history.back()}
        handleStep={handleStep}
      />
    );
  };


  const step2 = () => {
    return (
      <Step2
        strategy={strategy}
        setCR={setCR}
        tokenSupplyObj={tokenSupplyObj}
        tokenBorrowObj={tokenBorrowObj}
        sliderValue={sliderValue}
        mcr={mcr}
        setSliderValue={setSliderValue}
        handleStep={handleStep}
        handleStepBack={handleStepBack}
        aprValues={aprValues}
        hasStaked={hasStaked.value}
      />
    );
  };

  const step3 = () => {
    return (
      <Step3
        strategy={strategy}
        tokenSupplyObj={tokenSupplyObj}
        tokenBorrowObj={tokenBorrowObj}
        sliderValue={sliderValue}
        onClose={() => { }}
        handleStepBack={handleStepBack}
        onSuccess={() => window.location.href = '/#/strategies/your-position'}
        aprValues={aprValues}
      />
    );
  };

  const steps = getSteps();
  const PageBar = steps.map((item: string, index: number) => {
    return (
      <div className={`${index === activeStep ? 'step-active' : 'step-inactive'}`}>
        <span className={`${index === activeStep ? 'step-active-text' : 'step-inactive-text'}`}>
          {item}
        </span>
      </div>
    );
  });

  const handleStep = () => {
    setActiveStep(activeStep + 1);
    const newCompleted = new Set(completed);
    newCompleted.add(activeStep);
    setCompleted(newCompleted);
  };

  const handleStepBack = () => {
    setActiveStep(activeStep - 1);
    const newCompleted = new Set(completed);
    newCompleted.delete(activeStep);
    setCompleted(newCompleted);
  };

  if (Object.keys(tokenSupplyObj).length === 0) return <div />

  return (
    <div>
      <div className={'material-secondary'}>
        <MainBar>{PageBar}</MainBar>
        <Dividers />
        {getStepContent(activeStep)}
      </div>
    </div>
  );
};

export default CreatePosition;

const Dividers = styled.div`
  height: 0px;
  border: 1px solid rgba(255, 255, 255, 0.08);
  margin-top: 24px;
  margin-bottom: 24px;
`;

const MainBar = styled.div`
  display: flex;
  flex-direction: row;
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.08);
  height: 24px;
  margin-bottom: 24px;
  @media (max-width: 600px) {
    height: 48px;
  }
`;
