import React, { useMemo, useState } from 'react';
import Loader from 'react-spinners/PulseLoader';

import config from '../../../config';
import useCreateDSProxy from '../../../hooks/callbacks/useCreateDSProxy';
import useGetLeverageAccount from '../../../hooks/state/useGetLeverageAccount';
import { ZERO_ADDRESS } from '../../../protocol/constants';
import { useGetActiveChainId } from '../../../state/chains/hooks';
import Button from '../../../theme/components/Button';
import Modal from '../../../theme/components/Modal';
import TextWrapper from '../../../theme/components/TextWrapper';
import { IModalProps } from '../../../utils/interface';
import InfoTip from "../../../theme/components/InfoTip";

interface IProps {
  onSuccess: () => void;
}

enum DSProxyBuildTxState {
  NOT_COMPLETED,
  PENDING,
  COMPLETED,
}

const SignModal = (props: IModalProps & IProps) => {
  const [dsProxyBuildTxState, setDSProxyBuildTxState] = useState<DSProxyBuildTxState>(
    DSProxyBuildTxState.NOT_COMPLETED,
  );

  const { open, onClose, onSuccess } = props;
  const chainId = useGetActiveChainId();

  const leverageAccount = useGetLeverageAccount();

  const createDSProxyAction = useCreateDSProxy();

  const onSuccessCB = () => {
    setDSProxyBuildTxState(DSProxyBuildTxState.COMPLETED);
    onSuccess();
  };

  const onCreateDSProxy = () => {
    setDSProxyBuildTxState(DSProxyBuildTxState.PENDING);
    createDSProxyAction(onSuccessCB, () =>
      setDSProxyBuildTxState(DSProxyBuildTxState.NOT_COMPLETED),
    );
  };

  const etherscanUrl = useMemo(() => {
    return `${config[chainId].etherscanUrl}/address/${leverageAccount.value}`;
  }, [chainId, leverageAccount]);

  const FetchingDetails = () => {
    return (
      <TextWrapper
        text={
          <div>
            Fetching your details please hold on
            <Loader color={'#ffffff'} loading={true} size={8} margin={2} />
          </div>
        }
        align={'center'}
      />
    );
  };

  return (
    <Modal
      closeButton
      open={open}
      handleClose={onClose}
      title={
        leverageAccount.value === ZERO_ADDRESS
          ? 'Create a Strategy Account'
          : 'Continue with Strategy'
      }
    >
      {leverageAccount.isLoading ? (
        <FetchingDetails />
      ) : (
        <div>
          <TextWrapper
            text={
              leverageAccount.value === ZERO_ADDRESS && !leverageAccount.isLoading ? (
                "To use the various leverage strategies, we'll need you to create a leverage account. " +
                'Click on the button below to create a leverage account.'
              ) : (
                <div>
                  You have a leverage account controlled by you.
                  <br />
                  <br />
                  <div style={{ textAlign: 'center' }}>
                    <a
                      href={etherscanUrl}
                      rel="noopener noreferrer"
                      target="_blank"
                      className={'links'}
                    >
                      {leverageAccount.value}
                    </a>
                  </div>
                  <br />
                  All leveraged positions will now be made using this account. Please make sure
                  you have understood about all the{' '}
                  <a
                    href="https://docs.arthcoin.com/protocol/risks-and-security"
                    className={'links'}
                  >
                    risks
                  </a>{' '}
                  and that you have read and understood the documentation on{' '}
                  <a
                    href="https://docs.arthcoin.com/tutorials/leverage-strategies"
                    className={'links'}
                  >
                    how leverage works
                  </a>
                  .
                  <br />
                  <br />
                  Do not invest more than what you are willing to lose and you acknowledge that
                  you will take responsibility for any losses.
                </div>
              )
            }
          />
        </div>
      )}
      <div className={'m-t-24'}>
        <InfoTip
          type={'Error'}
          msg={'Please be aware that this feature is currently experimental and there is a risk you could lose all your funds. Only invest funds that you can afford to lose.'}
        />
      </div>
      <div className={'m-t-32'}>
        <Button
          loading={
            leverageAccount.isLoading || dsProxyBuildTxState === DSProxyBuildTxState.PENDING
          }
          disabled={
            dsProxyBuildTxState === DSProxyBuildTxState.PENDING ||
            dsProxyBuildTxState === DSProxyBuildTxState.COMPLETED
          }
          text={
            leverageAccount.value === ZERO_ADDRESS
              ? 'Create a leverage account'
              : "Yes I understand. Let's Continue!"
          }
          onClick={leverageAccount.value === ZERO_ADDRESS ? onCreateDSProxy : onSuccess}
          tracking_id={leverageAccount.value === ZERO_ADDRESS
            ? 'create_account'
            : 'continue_open_position'}
          tracking_params={{ action: 'confirm' }}
        />
      </div>
    </Modal>
  );
};

export default SignModal;
