import { BigNumber } from 'ethers';
import React, { useEffect, useMemo, useState } from "react";

import Grid from '@material-ui/core/Grid';

import useTokenBalance from '../../../hooks/state/useTokenBalance';
import useCore from '../../../hooks/useCore';
import { StrategiesStaticInfo } from '../../../protocol/config';
import { useGetActiveChainId } from '../../../state/chains/hooks';
import Button from '../../../theme/components/Button';
import CollateralDropDown from '../../../theme/components/CollateralDropDown';
import Input from '../../../theme/components/Input';
import InputContainer from '../../../theme/components/InputContainer';
import { formatToBN, getDisplayBalance } from "../../../utils/formatBalance";
import { troveDetails } from '../../../utils/tokenDetails';
import { ITokenObj } from "./CreatePosition";
import InfoTip from "../../../theme/components/InfoTip";
import TextWrapper from "../../../theme/components/TextWrapper";
import theme from "../../../theme";
import { BNZERO } from "../../../protocol/constants";
import useCollateralPriceFeed from "../../../hooks/state/useCollateralPriceFeed";

interface Iprops {
  strategy: StrategiesStaticInfo;
  tokenSupplyObj: ITokenObj;
  setTokenSupplyObj: (key: string, value: string) => void;
  onClose: () => void;
  handleStep: () => void;
}

const DashboardModal = (props: Iprops) => {
  const { strategy, tokenSupplyObj, setTokenSupplyObj, handleStep, onClose } = props;

  const core = useCore();
  const activeChainId = useGetActiveChainId();
  const troveDetail = troveDetails(strategy.trove, activeChainId);
  let [tokenBalanceObj, setTokenBalanceObj] = useState<{ [name: string]: BigNumber }>({});
  let [tokenValueGMUObj, setTokenValueGMUObj] = useState<{ [name: string]: BigNumber }>({});

  //Start MinCollateralLogic
  const dToken = core.tokens(strategy.depositTokens[0], activeChainId);
  const collateralGMUPrice = useCollateralPriceFeed(strategy.depositTokens[0], strategy.contract);

  const GMUOfInput = useMemo(() => {
    return collateralGMUPrice.value
      .mul(formatToBN(tokenSupplyObj[strategy.depositTokens[0]], dToken.decimal))
      .div(BigNumber.from(10).pow(dToken.decimal));
  }, [collateralGMUPrice.value, dToken.decimal, strategy.depositTokens, tokenSupplyObj])

  //End MinCollateralLogic

  useEffect(() => {
    strategy.depositTokens.forEach((t) => {
      const bal = tokenBalanceObj;
      bal[t] = BNZERO;
      setTokenBalanceObj(bal);
    })
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [strategy]);

  useEffect(() => {
    strategy.depositTokens.forEach((t) => {
      const bal = tokenValueGMUObj;
      bal[t] = BNZERO;
      setTokenValueGMUObj(bal);
    })
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [strategy]);

  const isError = useMemo(() => {
    let checks = 0;

    for (const [key, value] of Object.entries(tokenSupplyObj)) {
      const token = core.tokens(key, activeChainId);
      if (value !== '') checks++;
      if (tokenBalanceObj[key] && Number(tokenBalanceObj[key]) && tokenBalanceObj[key].gte(formatToBN(tokenSupplyObj[key], token.decimal))) {
        checks++;
      }
    }

    if (GMUOfInput.gte(formatToBN(strategy.minCollateralGMU, dToken.decimal))) checks++;

    return checks !== ((strategy.depositTokens.length * 2) + 1);
  }, [GMUOfInput, activeChainId, core, dToken.decimal, strategy.depositTokens.length, strategy.minCollateralGMU, tokenBalanceObj, tokenSupplyObj]);

  const setInputs = (value: string) => {
    strategy.depositTokens.forEach((t) => setTokenSupplyObj(t, value));
  }

  const Inputs = (t: string) => {
    const token = core.tokens(t, activeChainId);
    const tokenBalance = useTokenBalance(token);

    if (!tokenBalance.isLoading && tokenBalanceObj[t] && !tokenBalanceObj[t].eq(tokenBalance.value)) {
      let value = tokenBalanceObj
      value[t] = tokenBalance.value
      setTokenBalanceObj({ ...value })
    }

    /*if (!collateralGMUPrice.isLoading && tokenValueGMUObj[t]) {
      let data = tokenValueGMUObj;
      const GMUOfInput =
        collateralGMUPrice.value
          .mul(formatToBN(tokenSupplyObj[t], token.decimal))
          .div(BigNumber.from(10).pow(token.decimal));
      if (!tokenBalanceObj[t].eq(GMUOfInput)) {
        data[t] = GMUOfInput;
        setTokenValueGMUObj({ ...data })
      }
    }*/

    return (
      <div className={'m-b-16'}>
        <div className="m-b-8">
          <InputContainer
            label={'How much collateral would you like to supply?'}
            dataLabel={'Balance:'}
            dataValueLoading={tokenBalance.isLoading}
            dataValue={`${Number(
              getDisplayBalance(tokenBalance.value, token.decimal, 6),
            ).toLocaleString('en-US', { minimumFractionDigits: 3 })}`}
          >
            <div className="single-line-center-between">
              <Input
                disabled={tokenBalance.isLoading}
                value={tokenSupplyObj[t]}
                setValue={(value) => {
                  // setTokenSupplyObj(t, value);
                  setInputs(value);
                }}
                maxTag={true}
                onMaxClick={() => {
                  setInputs(Number(getDisplayBalance(tokenBalance.value, token.decimal, 6)).toString());
                  /*setTokenSupplyObj(t,
                    Number(getDisplayBalance(tokenBalance.value, 18, 18)).toString(),
                  );*/
                }}
              />
              <CollateralDropDown selectedCollateral={t} />
            </div>
            {tokenBalance.value.lt(formatToBN(tokenSupplyObj[t], token.decimal)) && <TextWrapper
              text={`Input cannot be greater than your ${t} balance`}
              Fcolor={theme.color.red[300]}
              fontSize={12}
              className={'m-t-4'}
            />}
          </InputContainer>
        </div>
      </div>
    )
  }


  return (
    <div>
      <InfoTip
        type={'Info'}
        msg={'This is the initial collateral that you will supply to open a position for this particular strategy. '
          + 'This collateral is returned to you if your position yields a net profit.'}
        className={'m-b-24'}
      />

      {strategy.depositTokens.map((t) => Inputs(t))}

      <div className={'m-t-32'}>
        <div>
          {GMUOfInput.lt(formatToBN(strategy.minCollateralGMU)) &&
            <InfoTip
              type={'Error'}
              msg={`Input amount should be greater than the min collateral amount (${strategy.minCollateralGMU} GMU) / $${strategy.minCollateralGMU * 2}`}
              className={'m-b-24'}
            />
          }
        </div>
        <Grid container spacing={2} direction={'row'}>
          <Grid item lg={6} md={6} sm={12} xs={12}>
            <Button
              variant={'transparent'}
              onClick={() => onClose()}
              tracking_id={'strategy_step1'}
              tracking_params={{
                action: 'cancel',
                collateral: strategy.trove,
                other: tokenSupplyObj,
              }}
            >
              Cancel
            </Button>
          </Grid>
          <Grid item lg={6} md={6} sm={12} xs={12}>
            <Button
              onClick={handleStep}
              disabled={isError}
              tracking_id={'strategy_step1'}
              tracking_params={{
                action: 'confirm',
                collateral: strategy.trove,
                other: tokenSupplyObj,
              }}
            >
              Next
            </Button>
          </Grid>
        </Grid>
      </div>
    </div>
  );
};

export default DashboardModal;
