import React, { useMemo } from "react";
import { useMediaQuery } from 'react-responsive';
import styled from 'styled-components';

import { Slider } from '@material-ui/core';
import Grid from '@material-ui/core/Grid';
import { Theme, createStyles, makeStyles, withStyles } from '@material-ui/core/styles';

import SlippageContainer from '../../../components/SlippageContainer';
import { StrategiesStaticInfo } from '../../../protocol/config';
import Button from '../../../theme/components/Button';
import IconLoader from '../../../theme/components/IconLoader';
import TextWrapper from '../../../theme/components/TextWrapper';
import { ITokenObj, minSlider } from "./CreatePosition";
import InfoTip from "../../../theme/components/InfoTip";
import theme from "../../../theme";
import DataField from "../../../theme/components/DataField";
import { State } from "../../../hooks/state/strategies/useGetAPRValue";
import { getDisplaycrParameters } from "../../../utils/crParameters";

interface IProps {
  strategy: StrategiesStaticInfo;
  tokenSupplyObj: ITokenObj;
  tokenBorrowObj: ITokenObj;
  setCR: (val: any) => void;
  mcr: number;
  sliderValue: number;
  setSliderValue: (value: any) => void;
  handleStep: () => void;
  handleStepBack: () => void;
  aprValues: State;
  hasStaked: boolean;
}

const PrettoRestrictSlider = withStyles({
  root: {
    width: '100%',
  },
  thumb: {
    height: 10,
    width: 10,
    border: '2px solid currentColor',
    color: '#FFA981',
    marginTop: -3.5,
    marginLeft: -3,
    '&:focus, &:hover, &$active': {
      boxShadow: 'inherit',
    },
  },
  active: {},
  valueLabel: {
    left: 'calc(-100% - 5px)',
  },
  marked: {
    color: 'red',
  },
  markLabel: {},
  track: {
    height: 3,
    borderRadius: 3,
    color: '#FFA981',
  },
  rail: {
    height: 3,
    borderRadius: 3,
    color: '#D74D26',
  },
  markLabelActive: {
    fontStyle: 'normal',
    fontWeight: 300,
    fontSize: '12px',
    lineHeight: '130%',
    textAlign: 'center',
    color: 'rgba(255, 255, 255, 0.88)',
  },
  mark: {
    color: 'transparent',
  },
})(Slider);

const useSliderStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      width: '100%',
    },
    margin: {
      height: theme.spacing(3),
    },
  }),
);

const DashboardModal = (props: IProps) => {
  const {
    strategy,
    tokenSupplyObj,
    tokenBorrowObj,
    sliderValue,
    setSliderValue,
    mcr,
    handleStep,
    handleStepBack,
    aprValues,
    hasStaked,
  } = props;

  const sliderClasses = useSliderStyles();
  const isMobile = useMediaQuery({ maxWidth: '600px' });
  // const price = useCollateralPriceFeed(strategy.depositToken);

  // const inAmount = useGetInAmountOnUni(
  //   strategyDetail.          routerAddress,
  //   BigNumber.from(parseUnits(tokenBorrow, 18)),
  //   strategyDetail.inputApproximationPath.map((i) => core.tokens(i, activeChainId)?.address),
  // );

  /* useEffect(() => {
     if (!tokenSupplyObj || !tokenBorrowObj) {
       setCR('0');
       return;
     }
     // const userSupplyInBN = formatToBN(tokenSupply, 18);
     // const userLeveragedSupplyInBN = formatToBN(tokenBorrow, 18);
     // const totalUserSupplyInBN = userSupplyInBN.add(userLeveragedSupplyInBN);
     // const crInBN = totalUserSupplyInBN.mul(price.value).mul(100).div(inAmount.value);
     // setCR(getDisplayBalance(crInBN, 18, 18));
   }, [tokenSupply, tokenBorrow, setCR]);*/

  function valuetext(value: number) {
    return `${value}`;
  }

  const handleSliderChange = (event: any, value: any) => {
    if (hasStaked) {
      setSliderValue(value);
    } else if (value <= strategy.maxNonStakersLeverage) {
      setSliderValue(value);
    }
  };

  const leveragedApr = useMemo(() => {
    if (aprValues.isLoading) {
      return {
        isLoading: true,
        value: '-'
      }
    } else {
      if (aprValues.value.totalAPR === '-' || !Number(aprValues.value.totalAPR)) {
        return {
          isLoading: false,
          value: '-'
        }
      } else {
        return {
          isLoading: false,
          value: (Number(aprValues.value.totalAPR) * sliderValue).toPrecision(3)
        }
      }
    }
  }, [aprValues.isLoading, aprValues.value.totalAPR, sliderValue]);

  const leveragedTradingFee = useMemo(() => {
    return {
      isLoading: false,
      value: (Number(0.3) * sliderValue).toPrecision(3)
    }
  }, [sliderValue])

  return (
    <div>
      <InfoTip
        type={'Info'}
        msg={'Choose how much leverage you would like to take. A higher leverage yields you more profits but increases the risk as well.'}
        className={'m-b-12'}
      />
      {/* <InfoTip
        type={'Warning'}
        msg={'As currently we are in alpha phase, so max Leverage for now is restricted to 5x '}
        className={'m-b-24'}
      /> */}
      <InputLabel>You will supply</InputLabel>
      {
        strategy.depositTokens.map(t => (
          <BoxedLabels>
            <div className="single-line-center-start">

              <IconLoader iconName={t} iconType="tokenSymbol" width={24} />
              <TextWrapper text={t} fontWeight={600} className={'m-l-4'} />
            </div>
            <TextWrapper text={Number(tokenSupplyObj[t]).toLocaleString()} fontWeight={600} />
          </BoxedLabels>
        ))
      }
      <Dividers />
      <div>
        <div className="single-line-center-between m-t-12 m-b-10">
          <div className="single-line-center-start">
            <TextWrapper text={'Select Leverage'} fontSize={12} />
            <div className="tags m-l-4">
              <TextWrapper text={`${sliderValue.toString()} X`} fontSize={12} />
            </div>
          </div>
          <div className="single-line-center-start">
            <TextWrapper text={'Slippage'} fontSize={12} className={'p-b-8 m-r-8'} />
            <SlippageContainer />
          </div>
        </div>
        <div className={sliderClasses.root}>
          <PrettoRestrictSlider
            defaultValue={minSlider}
            value={sliderValue}
            getAriaValueText={valuetext}
            valueLabelFormat={valuetext}
            onChange={handleSliderChange}
            aria-label="pretto slider"
            step={0.01}
            marks
            min={minSlider}
            max={strategy.maxLeverage}
            valueLabelDisplay="off"
          />
        </div>
        <div className="single-line-center-between m-b-24">
          <TextWrapper text={`${minSlider}x`} fontSize={12} />
          <TextWrapper text={`${strategy.maxLeverage}x`} fontSize={12} />
        </div>
        {!hasStaked && sliderValue > (strategy.maxNonStakersLeverage - 0.2) && <InfoTip
          type={"Warning"}
          msg={<div>
            You are restricted to take just upto {strategy.maxNonStakersLeverage} of leverage. you can increase this by <a className={"links"} href={"/#/farming"}> staking</a>
          </div>}
          className={"m-b-12"}
        />}
      </div>
      <Dividers />
      <div>

        <InputLabel>You will gain exposure to</InputLabel>
        {
          strategy.borrowTokens.map(t => (
            <BoxedLabels>
              <div className="single-line-center-start">
                <IconLoader iconName={t} iconType="tokenSymbol" width={24} />
                <TextWrapper text={t} fontWeight={600} className={'m-l-4'} />
              </div>
              <TextWrapper text={Number(tokenBorrowObj[t]).toLocaleString()} fontWeight={600} />
            </BoxedLabels>
          ))
        }
      </div>
      <div className={'m-t-24'}>
        <DataField
          label={'Min Collateral Ratio'}
          labelToolTip={``}
          value={`${mcr.toFixed(3)}%`}
          labelFontColor={theme.color.transparent[100]}
          labelFontSize={12}
          className={'m-b-8'}
        />
        <DataField
          label={'Trading fee'}
          isValueLoading={leveragedTradingFee.isLoading}
          value={`${leveragedTradingFee.value}%`}
          labelFontSize={12}
          labelFontColor={theme.color.transparent[100]}
          className={'m-b-8'}
        />
        <DataField
          label={'APR'}
          labelToolTip={''}
          isValueLoading={leveragedApr.isLoading}
          value={`${leveragedApr.value}%`}
          labelFontSize={12}
          labelFontColor={theme.color.transparent[100]}
          valueFontColor={theme.color.primary[300]}
        />
      </div>
      <div className="m-t-32">
        <Grid container spacing={2} direction={isMobile ? 'column-reverse' : 'row'}>
          <Grid item lg={6} md={6} sm={12} xs={12}>
            <Button
              variant={'transparent'}
              onClick={handleStepBack}
              tracking_id={'strategy_step2'}
              tracking_params={{
                action: 'cancel',
                collateral: strategy.trove,
              }}
            >
              Back
            </Button>
          </Grid>
          <Grid item lg={6} md={6} sm={12} xs={12}>
            <Button
              onClick={handleStep}
              tracking_id={'strategy_step2'}
              disabled={sliderValue === 1}
              tracking_params={{
                action: 'confirm',
                collateral: strategy.trove,
                other: {
                  leverage: sliderValue,
                  borrow: tokenBorrowObj,
                }
              }}
            >Borrow</Button>
          </Grid>
        </Grid>
      </div>
    </div>
  );
};

export default DashboardModal;

const InputLabel = styled.p`
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  color: rgba(255, 255, 255, 0.64);
  margin-bottom: 8px;
`;

const Dividers = styled.div`
  height: 0;
  border: 1px solid rgba(255, 255, 255, 0.08);
  margin-top: 24px;
  margin-bottom: 24px;
`;

const BoxedLabels = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  background: rgba(255, 255, 255, 0.08);
  height: 44px;
  border-radius: 6px;
  margin-bottom: 8px;
  padding: 0 12px 0 12px;
`;
