import React, { useMemo } from 'react';
import { useMediaQuery } from 'react-responsive';
import styled from 'styled-components';

import Grid from '@material-ui/core/Grid';

import useCore from '../../../hooks/useCore';
import { useGetActiveChainId } from '../../../state/chains/hooks';
import TextButton from '../../../theme/components/TextButton';

import PoolCard from './PoolCard';
import { useWallet } from "use-wallet";

const PoolTable = () => {
  const isMobile = useMediaQuery({ maxWidth: '600px' });
  const activeChainId = useGetActiveChainId();

  const core = useCore();
  const {account} = useWallet();
  const strategiesTypes = useMemo(() => core.getAllStrategies(activeChainId, account), [
    core,
    activeChainId,
    account
  ]);

  return (
    <div className={'mo-custom-container'}>
      <div className={'m-b-24'}>
        <Grid container className="m-t-24" spacing={2}>
          {strategiesTypes.map((strategy, index) => (
            <Grid item lg={4} md={6} sm={12} xs={12} key={index}>
              <PoolCard strategy={strategy} key={index} />
            </Grid>
          ))}
        </Grid>
      </div>
      {isMobile && <div className={"single-line-center-center"}>
        <TextButton
          onClick={() =>
            window.open(
              "https://docs.google.com/forms/d/e/1FAIpQLSdOoo2_88RuRZoDfOEj8B_fN7r8DUmXh9mZH5Mkj59WLzrQ6Q/viewform?usp=sf_link"
            )
          }
          text={"Request Pool"}
        />
      </div>}
    </div>
  );
};

export default PoolTable;

const DataContainer = styled.div`
  background: rgba(255, 255, 255, 0.02);
  backdrop-filter: blur(70px);
  border-radius: 6px;
  padding: 24px 32px;
  @media (max-width: 600px) {
    background: transparent;
    backdrop-filter: none;
    border-radius: 6px;
    padding: 32px 0;
  }
`;

const CustomTableHeading = styled.span`
  display: flex;
  align-items: center;
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 150%;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  color: rgba(255, 255, 255, 0.32);
  opacity: 0.64;
  text-align: left;
`;
