import { useCallback, useEffect, useState } from 'react';
import { useWallet } from 'use-wallet';

import { useBlockNumber } from '../../../state/application/hooks';
import { useGetActiveChainId } from '../../../state/chains/hooks';
import useCore from '../../useCore';
import useGetLeverageAccount from "../useGetLeverageAccount";

type State = {
  isLoading: boolean;
  value: boolean;
};

export default (contractPrefix: string) => {
  const [value, setValue] = useState<State>({ isLoading: true, value: false });

  const core = useCore();
  const { account, chainId } = useWallet();
  const blockNumber = useBlockNumber();
  const activeChainId = useGetActiveChainId();
  const leverageAccount = useGetLeverageAccount();

  const fetchValue = useCallback(async () => {
    if (!account && leverageAccount.isLoading) {
      setValue({ isLoading: true, value: false });
      return;
    }

    if (!leverageAccount.value && !leverageAccount.isLoading) {
      setValue({ isLoading: false, value: false });
      return;
    }

    const troveContract = core.getTroveManager(contractPrefix, activeChainId);
    console.log('ttttt', troveContract.address, troveContract.provider, chainId, activeChainId);
    const trove = await troveContract.Troves(leverageAccount.value);
    setValue({ isLoading: false, value: trove?.status === 1 });
  }, [account, leverageAccount.isLoading, leverageAccount.value, core, contractPrefix, activeChainId]);

  useEffect(() => {
    if (core.isUnlocked) {
      fetchValue().catch((err) => console.error(`Failed to fetch trove details isStrategyOpen: ${err}`));
    }
  }, [core.isUnlocked, core.myAccount, account, blockNumber, fetchValue, leverageAccount]);

  return value;
};
