import { BigNumber } from 'ethers';
import { useCallback, useMemo } from 'react';

import { StrategiesStaticInfo } from '../../../protocol/config';
import { DECIMALS_18 } from '../../../protocol/constants';
import { useAddPopup } from '../../../state/application/hooks';
import { useGetActiveChainId } from '../../../state/chains/hooks';
import { useTransactionAdder } from '../../../state/transactions/hooks';
import formatErrorMessage from '../../../utils/formatErrorMessage';
import { findHintsForNominalCollateralRatio } from '../../state/hinthelpers/helper';
import useGetLeverageAccount from '../../state/useGetLeverageAccount';
import useCore from '../../useCore';

export default function (strategy: StrategiesStaticInfo) {
  const core = useCore();
  const activeChainId = useGetActiveChainId();
  const addPopup = useAddPopup();
  const addTransaction = useTransactionAdder();

  const leverageAccount = useGetLeverageAccount();

  const leverageAccountContract = useMemo(
    () =>
      leverageAccount.value
        ? core.getContractAt('LeverageAccount', leverageAccount.value)
        : null,
    [core, leverageAccount.value],
  );

  const strategyContract = useMemo(() => core.contracts(strategy.contract, activeChainId), [
    activeChainId,
    core,
    strategy.contract,
  ]);

  return useCallback(
    async (onSuccess?: () => void, errorHandler?: () => void): Promise<void> => {
      try {
        const tx = await leverageAccountContract.approveStrategy(strategyContract.address);

        addTransaction(tx, {
          summary: `Approve ${strategy.displayName} strategy to use your leverage account`,
        });

        const response = await tx.wait();

        if (errorHandler && response.status !== 1) errorHandler();
        if (onSuccess && response.status === 1) onSuccess();
      } catch (e) {
        if (errorHandler) errorHandler();

        console.log('Error', e);
        addPopup({
          error: {
            message: formatErrorMessage(e?.data?.message || e?.message),
            stack: e?.stack,
          },
        });
      }
    },
    [
      leverageAccountContract,
      strategyContract.address,
      addTransaction,
      strategy.displayName,
      addPopup,
    ],
  );
}
