import React, { useState } from 'react';
import { useMediaQuery } from 'react-responsive';
import { StrategiesStaticInfo } from '../../../protocol/config';

import SignModal from '../modals/SignModal';

import MobilePoolCard from './MobilePoolCard';
import isStrategyOpen from "../../../hooks/state/strategies/isStrategyOpen";
import { trackMixpanel } from "../../../utils/mixpanel";
import { useGetActiveChainId } from "../../../state/chains/hooks";

interface IProps {
  strategy: StrategiesStaticInfo;
}

const PoolCard = (props: IProps) => {
  const { strategy } = props;
  const [openSignModal, setOpenSignModal] = useState<boolean>(false);

  const redirection = `/#/strategies/add/${strategy.trove}`;
  const hasPosition = isStrategyOpen(strategy.trove);
  const chainId = useGetActiveChainId();

  const onTroveclick = () => {
    if (hasPosition.isLoading) {
    } else if (hasPosition.value) {
      trackMixpanel(
        'view_position',
        chainId,
      )
      window.location.href = '/#/strategies/your-position'
    } else {
      const isSigned = false;
      if (!isSigned) {
        trackMixpanel(
          'open_position',
          chainId,
          {
            action: 'initial',
          }
        )
        setOpenSignModal(true);
      } else {
        window.location.href = redirection;
      }
    }
  };

  return (
    <div>
      <SignModal
        open={openSignModal}
        onClose={() => setOpenSignModal(false)}
        onSuccess={() => (window.location.href = redirection)}
      />
      <MobilePoolCard strategy={strategy} onTroveClick={onTroveclick} />
    </div>
  );
};

export default PoolCard;
