import config from "../config";
import { Mixpanel } from "../analytics/Mixpanel";
import { tracking_params } from "../theme/components/Button";

export const trackMixpanel = (tracking_id: string, chainId: number, t_params: tracking_params = {}) => {
  const params = {
    networkName: config[chainId]?.networkDisplayName || '',
    ...t_params,
  };

  if (tracking_id) {
    Mixpanel.track(`buttonClick:${tracking_id.toLowerCase()}`, params);
  }
};
