import * as Sentry from '@sentry/react';
import { BigNumber } from 'ethers';
import { useCallback, useMemo } from 'react';

import { StrategiesStaticInfo } from '../../../protocol/config';
import { DECIMALS_18 } from '../../../protocol/constants';
import { useAddPopup } from '../../../state/application/hooks';
import { useGetActiveChainId } from '../../../state/chains/hooks';
import { useTransactionAdder } from '../../../state/transactions/hooks';
import formatErrorMessage from '../../../utils/formatErrorMessage';
import { findHintsForNominalCollateralRatio } from '../../state/hinthelpers/helper';
import useCore from '../../useCore';

export default function (
  strategy: StrategiesStaticInfo,
  principalAmounts: { [name: string]: string },
  borrowingAmounts: { [name: string]: string },
  totalExposure: BigNumber,
  borrowingFeeRate: BigNumber,
) {
  const core = useCore();
  const activeChainId = useGetActiveChainId();
  const addPopup = useAddPopup();
  const addTransaction = useTransactionAdder();

  const strategyContract = useMemo(() => core.contracts(strategy.contract, activeChainId), [
    activeChainId,
    core,
    strategy.contract,
  ]);

  return useCallback(
    async (callback?: () => void, errorHandler?: () => void): Promise<void> => {
      try {
        const contract = strategyContract.connect(core.signer());

        const converTokenBalToBN = (tokenName: string, value: string) => {
          const token = core.tokens(tokenName, core._activeNetwork);
          const decimals = BigNumber.from(10).pow(token.decimal);
          return BigNumber.from(Math.floor(Number(value) * 100000))
            .mul(decimals)
            .div(100000)
            .toString();
        };

        const convertMapping = (mapping: { [name: string]: string }) => {
          const keys = Object.keys(mapping);
          return keys.map((key) => converTokenBalToBN(key, mapping[key]));
        };

        const principalCollateral = [...convertMapping(principalAmounts), '0'];
        const params = [
          convertMapping(borrowingAmounts), // uint256 borrowedCollateral,
          principalCollateral, // uint256 principalCollateral,
          '0', // uint256 minExposure,
          '0', // uint256 maxBorrowingFee
        ];

        const tx = await contract.openPosition(...params);

        addTransaction(tx, {
          summary: `Open leverage position for ${strategy.displayName} strategy`,
        });

        const response = await tx.wait();

        if (errorHandler && response.status !== 1) {
          Sentry.captureException({
            response,
            ...strategy,
            ...principalAmounts,
            ...borrowingAmounts,
          });
          errorHandler();
        }
        if (callback && response.status === 1) callback();
      } catch (e) {
        Sentry.captureException({
          e,
          ...strategy,
          ...principalAmounts,
          ...borrowingAmounts,
        });
        if (errorHandler) errorHandler();
        addPopup({
          error: {
            message: formatErrorMessage(e?.data?.message || e?.message),
            stack: e?.stack,
          },
        });
      }
    },
    [
      strategyContract,
      core,
      principalAmounts,
      borrowingAmounts,
      addTransaction,
      strategy,
      addPopup,
    ],
  );
}
