import React, { useEffect, useMemo, useState } from "react";
import { Grid } from '@material-ui/core';

import { StrategiesStaticInfo } from '../../../protocol/config';
import { ITokenObj } from "./CreatePosition";
import { useSlippage } from "../../../state/slippage/hooks";
import ApproveButtons from "./ApproveButtons";
import Button from "../../../theme/components/Button";
import useApproveStrategy from "../../../hooks/callbacks/strategies/useApproveStrategy";
import { BigNumber } from "ethers";
import useGetBorrowingFeeRateWithDecay from "../../../hooks/state/troveManager/useGetBorrowingFeeRateWithDecay";
import useOpenStrategy from "../../../hooks/callbacks/strategies/useOpenStrategy";
import useIsStrategyApproved from "../../../hooks/state/strategies/useIsStrategyApproved";

interface IProps {
  onClose: () => void;
  handleStepBack: () => void;
  onSuccess: () => void;
  sliderValue: number;
  tokenSupplyObj: ITokenObj;
  tokenBorrowObj: ITokenObj;
  strategy: StrategiesStaticInfo;
}

interface IApproval { [name: string]: 'Approved' | 'IsApproving' | 'NotApproved' }

const ConfirmButton = (props: IProps) => {
  const { value: slippage } = useSlippage();
  const [approvalStatus, setApprovalStatus] = useState<IApproval>({});
  const [isApprovingStrategy, setIsApprovingStrategy] = useState<boolean>(false);

  const strategyApprovalStatus = useIsStrategyApproved(props.strategy);

  useEffect(() => {
    if (approvalStatus === {}) {
      props.strategy.depositTokens.map((token) => {
        const status = approvalStatus;
        status[token] = 'NotApproved';
        setApprovalStatus({ ...status });
      })
    }
  }, [approvalStatus, props.strategy.depositTokens])

  const approvalButtons = useMemo(() => props.strategy.depositTokens.map((token) => {
    const onlyOneCollateral = props.strategy.depositTokens.length === 1
    return (
      <Grid item lg={onlyOneCollateral ? 12 : 6} md={onlyOneCollateral ? 12 : 6} sm={12} xs={12}>
        <ApproveButtons
          key={token}
          token={token}
          contract={props.strategy.contract}
          updateStatus={(data) => {
            if (approvalStatus[token] !== data) {
              const temp = approvalStatus;
              temp[token] = data;
              setApprovalStatus({ ...temp });
            }
          }}
        />
      </Grid>
    )
  }), [approvalStatus, props.strategy.contract, props.strategy.depositTokens])

  const approveStrategy = useApproveStrategy(props.strategy)

  const onApproveStrategy = () => approveStrategy(
    () => setIsApprovingStrategy(false),
    () => { },
  );

  const enableConfirm = useMemo(() => {
    let allApproved = 0;
    if (strategyApprovalStatus.isLoading) {
      return {
        value: false,
        isLoading: true,
      }
    }
    props.strategy.depositTokens.forEach((token) => {
      if (approvalStatus[token] === 'IsApproving') {
        return {
          value: false,
          isLoading: true,
        }
      }
    });
    props.strategy.depositTokens.forEach((token) => {
      if (approvalStatus[token] === 'Approved') {
        allApproved++;
      }
    });
    if (strategyApprovalStatus.isApproved) allApproved++;
    return {
      value: allApproved === props.strategy.depositTokens.length + 1,
      isLoading: false
    }
  }, [approvalStatus, props.strategy.depositTokens, strategyApprovalStatus])

  const borrowingFeeRate = useGetBorrowingFeeRateWithDecay(props.strategy.trove);

  const leverageHandler = useOpenStrategy(
    props.strategy,
    props.tokenSupplyObj,
    props.tokenBorrowObj,
    borrowingFeeRate.value,
    BigNumber.from(0)
  );

  const onConfirm = () => leverageHandler(props.onSuccess, props.onClose);

  return (
    <div className="m-t-32">
      {!enableConfirm.value && !strategyApprovalStatus.isLoading && !enableConfirm.isLoading &&
        <div>
          <Grid container spacing={2} direction={"row"}>
            {approvalButtons}
          </Grid>
          <Grid container spacing={2} direction={"row"}>
            <Grid item sm={12} xs={12}>
              <Button
                variant={"default"}
                onClick={() => {
                  setIsApprovingStrategy(true);
                  onApproveStrategy();
                }}
                disabled={strategyApprovalStatus.isApproved || strategyApprovalStatus.isLoading || isApprovingStrategy}
                loading={isApprovingStrategy}
                tracking_id={'strategy_approve_strategy'}
                tracking_params={{
                  collateral: props.strategy.trove,
                }}
              >
                {strategyApprovalStatus.isApproved ? 'Strategy Approved' : 'Approve Strategy'}
              </Button>
            </Grid>
          </Grid>
        </div>
      }
      <Grid container spacing={2} direction={'row'}>
        <Grid item lg={6} md={6} sm={12} xs={12}>
          <Button
            variant={'transparent'}
            onClick={props.handleStepBack}
            tracking_id={'strategy_step3'}
            tracking_params={{
              action: 'confirm',
              collateral: props.strategy.trove,
            }}
          >
            Back
          </Button>
        </Grid>
        <Grid item lg={6} md={6} sm={12} xs={12}>
          <Button
            onClick={onConfirm}
            disabled={!enableConfirm.value || enableConfirm.isLoading}
            tracking_id={'strategy_step3'}
            tracking_params={{
              action: 'confirm',
              collateral: props.strategy.trove,
              other: {
                supply: props.tokenSupplyObj,
                borrow: props.tokenBorrowObj,
                leverage: props.sliderValue,
              }
            }}
          >Confirm</Button>
        </Grid>
      </Grid >
    </div >
  );
};

export default ConfirmButton;
