import { BigNumber } from '@ethersproject/bignumber';
import { useCallback, useEffect, useMemo, useState } from "react";
import { useWallet } from 'use-wallet';

import { useBlockNumber } from '../../../state/application/hooks';
import { useGetActiveChainId } from '../../../state/chains/hooks';
import useCore from '../../useCore';
import useGetLeverageAccount from "../useGetLeverageAccount";
import { StrategiesStaticInfo } from "../../../protocol/config";

type State = {
  isLoading: boolean;
  isApproved: boolean;
};

export default (strategy: StrategiesStaticInfo) => {
  const [value, setValue] = useState<State>({ isLoading: false, isApproved: true });

  const core = useCore();
  const { account } = useWallet();
  const blockNumber = useBlockNumber();
  const chainId = useGetActiveChainId();
  const leverageAccount = useGetLeverageAccount();

  const leverageAccountContract = useMemo(
    () =>
      leverageAccount.value
        ? core.getContractAt('LeverageAccount', leverageAccount.value)
        : null,
    [core, leverageAccount.value],
  );

  const strategyContract = useMemo(() => core.contracts(strategy.contract, chainId), [chainId, core, strategy.contract]);

  const fetchValue = useCallback(async () => {
    if (leverageAccount.isLoading || leverageAccountContract === null) {
      setValue({ isLoading: true, isApproved: false });
      return;
    }

    const status = await leverageAccountContract.canExecute(strategyContract.address);

    setValue({ isLoading: false, isApproved: status });
  }, [leverageAccount, leverageAccountContract, strategyContract]);

  useEffect(() => {
    if (core.isUnlocked) {
      fetchValue().catch((err) => {
        setValue({ isLoading: false, isApproved: false });
        console.error(`Failed to fetch staking balance: ${err}`)
      });
    }
  }, [core.isUnlocked, blockNumber, account, fetchValue, leverageAccountContract, strategyContract, leverageAccount]);

  return value;
};
