import React, { useMemo } from "react";
import styled from 'styled-components';

import { StrategiesStaticInfo } from '../../../protocol/config';
import theme from '../../../theme';
import DataField from '../../../theme/components/DataField';
import { ITokenObj } from "./CreatePosition";
import ConfirmButton from "./ConfirmButton";
import TextWrapper from "../../../theme/components/TextWrapper";
import { useSlippage } from "../../../state/slippage/hooks";
import useGetAPRValue, { State } from "../../../hooks/state/strategies/useGetAPRValue";
import InfoTip from "../../../theme/components/InfoTip";

interface IProps {
  onClose: () => void;
  handleStepBack: () => void;
  onSuccess: () => void;
  sliderValue: number;
  tokenSupplyObj: ITokenObj;
  tokenBorrowObj: ITokenObj;
  strategy: StrategiesStaticInfo;
  aprValues: State;
}

const Step3 = (props: IProps) => {
  const {
    sliderValue,
    tokenSupplyObj,
    tokenBorrowObj,
    strategy,
    aprValues
  } = props;

  const { value: slippage } = useSlippage();

  const leveragedApr = useMemo(() => {
    if (aprValues.isLoading) {
      return {
        isLoading: true,
        value: '-'
      }
    } else {
      if (aprValues.value.totalAPR === '-' || !Number(aprValues.value.totalAPR)) {
        return {
          isLoading: false,
          value: '-'
        }
      } else {
        return {
          isLoading: false,
          value: (Number(aprValues.value.totalAPR) * sliderValue).toPrecision(3)
        }
      }
    }
  }, [aprValues.isLoading, aprValues.value.totalAPR, sliderValue]);

  const leveragedTradingFee = useMemo(() => {
    return {
      isLoading: false,
      value: (Number(0.3) * sliderValue).toPrecision(3)
    }
  }, [sliderValue]);

  const totalApr = useMemo(() => {
    if (aprValues.isLoading || leveragedTradingFee.isLoading) {
      return {
        isLoading: true,
        value: '-'
      }
    } else {
      if (isNaN(Number(aprValues.value.tradingAPR)) || isNaN(Number(aprValues.value.totalAPR))) {
        return {
          isLoading: false,
          value: '-'
        }
      } else {
        return {
          isLoading: false,
          value: (Number(leveragedApr.value) + Number(aprValues.value.tradingAPR) - Number(leveragedTradingFee.value) - Number(0.00)).toPrecision(3)
        }
      }
    }
  }, [aprValues.isLoading, aprValues.value.totalAPR, aprValues.value.tradingAPR, leveragedApr.value, leveragedTradingFee.isLoading, leveragedTradingFee.value])

  return (
    <div>
      <div>
        <div className={'single-line-start-between m-b-8'}>
          <TextWrapper
            text={'Principal Collateral'}
            fontWeight={600}
            Fcolor={theme.color.transparent[200]}
          />
          <div>
            {strategy.depositTokens.map((item) => {
              return (
                <TextWrapper
                  text={Number(tokenSupplyObj[item]).toLocaleString() + ` ${item}`}
                  fontWeight={600}
                  Fcolor={theme.color.transparent[100]}
                  className={'m-b-4'}
                  align={'right'}
                />
              )
            })}
          </div>
        </div>
        <div className={'single-line-start-between m-b-8'}>
          <TextWrapper
            text={'You will gain exposure to'}
            fontWeight={600}
            Fcolor={theme.color.transparent[200]}
          />
          <div>
            {strategy.borrowTokens.map((item) => {
              return (
                <TextWrapper
                  text={Number(tokenBorrowObj[item]).toLocaleString() + ` ${item}`}
                  fontWeight={600}
                  Fcolor={theme.color.transparent[100]}
                  className={'m-b-4'}
                  align={'right'}
                />
              )
            })}
          </div>
        </div>
        {/*<div className={'single-line-start-between m-b-8'}>
          <TextWrapper
            text={'Net Exposure'}
            fontWeight={600}
            Fcolor={theme.color.transparent[200]}
          />
          <div>
            {strategy.depositTokens.map((item) => {
              return (
                <TextWrapper
                  text={(Number(tokenBorrowObj[item]) + Number(tokenSupplyObj[item])) + ` ${item}`}
                  fontWeight={600}
                  Fcolor={theme.color.transparent[100]}
                  className={'m-b-4'}
                  align={'right'}
                />
              )
            })}
          </div>
        </div>*/}
      </div>
      <Dividers />
      <div>
        {/*<DataField
          label={'Collateral ratio'}
          value={Number(0).toLocaleString() + '%'}
          labelFontWeight={600}
          labelFontColor={theme.color.transparent[200]}
          valueFontWeight={600}
          valueFontColor={theme.color.transparent[100]}
          className={'m-b-8'}
        />*/}
        <DataField
          label={'Total leverage'}
          value={sliderValue + 'x'}
          labelFontWeight={600}
          labelFontColor={theme.color.transparent[200]}
          valueFontWeight={600}
          valueFontColor={theme.color.transparent[100]}
          className={'m-b-8'}
        />
      </div>
      <Dividers />
      <div>
        <DataField
          label={'Yield Farm APR'}
          value={`${leveragedApr.value}%`}
          labelToolTip={'This is the APR that you will be earning from the yield farm that you are farming.'}
          isValueLoading={leveragedApr.isLoading}
          labelFontWeight={600}
          labelFontColor={theme.color.transparent[200]}
          valueFontWeight={600}
          valueFontColor={theme.color.green[300]}
          className={'m-b-8'}
        />
        {/*<DataField
          label={'Trading Fees APR'}
          value={`${aprValues.value.tradingAPR}%`}
          labelToolTip={'This is the APY that you will be earning from trading fees (if you are providing liquidity)'}
          isValueLoading={aprValues.isLoading}
          labelFontWeight={600}
          labelFontColor={theme.color.transparent[200]}
          valueFontWeight={600}
          valueFontColor={theme.color.transparent[100]}
          className={'m-b-8'}
        />*/}
        <DataField
          label={'Borrowing Fee'}
          value={`0.0%`}
          labelToolTip={'This is the APR that you will pay to the protocol to open this position'}
          isValueLoading={aprValues.isLoading}
          labelFontWeight={600}
          labelFontColor={theme.color.transparent[200]}
          valueFontWeight={600}
          valueFontColor={theme.color.red[300]}
          className={'m-b-8'}
        />
        <DataField
          label={'Trading Fee'}
          value={`${leveragedTradingFee.value}%`}
          labelToolTip={'This is the APR that you will pay to the protocol to open this position'}
          isValueLoading={leveragedTradingFee.isLoading}
          labelFontWeight={600}
          labelFontColor={theme.color.transparent[200]}
          valueFontWeight={600}
          valueFontColor={theme.color.red[300]}
          className={'m-b-8'}
        />
        <DataField
          label={'Total APR'}
          value={`${totalApr.value}%`}
          labelToolTip={'This is the final APR that you will be earning from this position.'}
          isValueLoading={totalApr.isLoading}
          labelFontWeight={600}
          labelFontColor={theme.color.transparent[200]}
          valueFontWeight={600}
          valueFontSize={16}
          labelFontSize={16}
          valueFontColor={theme.color.primary[300]}
          className={'m-b-8'}
        />
      </div>
      <Dividers />
      <div>
        <DataField
          label={'Slippage Tolerance'}
          value={`${slippage}%`}
          labelFontWeight={600}
          labelFontColor={theme.color.transparent[200]}
          valueFontWeight={600}
          valueFontColor={theme.color.transparent[100]}
          className={'m-b-8'}
        />
        {/*<DataField
          label={'Est. Price impact on position value'}
          value={'87.9%'}
          labelFontWeight={600}
          labelFontColor={theme.color.transparent[200]}
          valueFontWeight={600}
          valueFontColor={theme.color.transparent[100]}
          className={'m-b-8'}
        />
        <DataField
          label={'Est. Price impact on users supplied value'}
          value={'87.9%'}
          labelFontWeight={600}
          labelFontColor={theme.color.transparent[200]}
          valueFontWeight={600}
          valueFontColor={theme.color.transparent[100]}
          className={'m-b-8'}
        />*/}
        {/* <InfoTip
          msg={'When you open this position you will lose your shit'}
          type={'Warning'}
        /> */}
      </div>
      <ConfirmButton
        onClose={props.onClose}
        handleStepBack={props.handleStepBack}
        onSuccess={props.onSuccess}
        sliderValue={props.sliderValue}
        tokenSupplyObj={props.tokenSupplyObj}
        tokenBorrowObj={props.tokenBorrowObj}
        strategy={props.strategy}
      />
    </div >
  );
};

export default Step3;

const Dividers = styled.div`
  height: 0px;
  border: 1px solid rgba(255, 255, 255, 0.08);
  margin-top: 24px;
  margin-bottom: 24px;
`;
