import { useCallback, useEffect, useState } from 'react';
import { useWallet } from 'use-wallet';

export interface State {
  isLoading: boolean;
  value: {
    supplyAPR: string,
    tradingAPR: string,
    borrowAPR: string,
    totalAPR: string,
  };
}

const initialState = {
  isLoading: true,
  value: {
    supplyAPR: '-',
      tradingAPR: '-',
      borrowAPR: '-',
      totalAPR: '-',
  }
}

const useGetAPRValue = (trove: string) => {
  const [value, setValue] = useState<State>(initialState);

  const { account } = useWallet();

  const fetchValues = useCallback(async () => {
    try {
      fetch(`https://api.arthcoin.com/apy/leverage?collateral=${trove}`)
        .then((data) => data.json())
        .then((data: any) => {
          setValue({
            isLoading: false,
            value: data
          });
        })
        .catch((error) => {
          setValue(initialState);
          console.error(`Failed to fetch apr values for ${trove}: ${error.stack}`);
          return 0;
        });
    } catch(e) {
      setValue(initialState);
    }
  }, [trove]);

  useEffect(() => {
    fetchValues().catch((err) => {
      setValue(initialState);
      console.error(
        `Failed to fetch apr values for ${trove}: ${err.stack} `
      )
    })
  }, [account, fetchValues, trove]);

  return value;
};

export default useGetAPRValue;
