import { useCallback, useEffect, useState } from 'react';
import { useWallet } from 'use-wallet';

import { useGetActiveChainId } from '../../state/chains/hooks';
import useCore from '../useCore';

type State = {
  isLoading: boolean;
  value: string;
};

export default () => {
  const [value, setValue] = useState<State>({ isLoading: true, value: '' });

  const core = useCore();
  const activeChainId = useGetActiveChainId();
  const { account } = useWallet();

  const fetchValue = useCallback(async () => {
    if (!account) {
      setValue({ isLoading: false, value: '' });
      return;
    }

    const proxyRegistryContract = core.getLeverageAccountRegistry(activeChainId);
    const proxyAddress = await proxyRegistryContract.accounts(account);

    setValue({
      isLoading: false,
      value: proxyAddress,
    });
  }, [account, core, activeChainId]);

  useEffect(() => {
    if (core.isUnlocked) {
      fetchValue().catch((err) => console.error(`Failed to fetch dsproxy %: ${err}`));
    }
  }, [core.isUnlocked, account, fetchValue, activeChainId]);

  return value;
};
