import React, { useMemo, useState } from "react";
import styled from 'styled-components';

import { StrategiesStaticInfo } from '../../../protocol/config';
import useCore from '../../../hooks/useCore';
import { useGetActiveChainId } from '../../../state/chains/hooks';

import theme from '../../../theme';
import DataField from '../../../theme/components/DataField';
import IconLoader from '../../../theme/components/IconLoader';
import TextButton from '../../../theme/components/TextButton';
import TextWrapper from '../../../theme/components/TextWrapper';
import { getDisplayBalance } from "../../../utils/formatBalance";
import ConfirmCloseModal from "../modal/CloseLoan";
import useStrategyEarnings from "../../../hooks/state/strategies/useStrategyEarnings";
import useStrategyExposure from "../../../hooks/state/strategies/useStrategyExposure";
import Numeral from "numeral";
import useGetTotalCollateralRatio from "../../../hooks/state/troveManager/useGetTotalCollateralRatio";

interface IProps {
  trove: any;
  strategy: StrategiesStaticInfo;
}

const MobilePositionCard = (props: IProps) => {
  const { strategy, trove } = props;
  const [confirmCloseModal, setConfirmCloseModal] = useState<boolean>(false);

  const core = useCore()
  const chainId = useGetActiveChainId();
  const rewards = useStrategyEarnings(strategy)
  const positions = useStrategyExposure(strategy, trove.deposit);
  const totalCollteralRatio = useGetTotalCollateralRatio(strategy.trove);

  const leverage = useMemo(() => {
    const collateralRatio = trove.collateralRatio.toNumber()
    return Math.floor((collateralRatio / (collateralRatio - 100)) * 1000)
  }, [trove.collateralRatio])

  const principalAmount = useMemo(() => {
    const totalInPrincipal = positions.value[0].add(positions.value[1])
    return totalInPrincipal.div(leverage).mul(1000)
  }, [leverage, positions.value])

  const depositTokens = useMemo(() => {
    return strategy.depositTokens.map(d => core.tokens(d, chainId))
  }, [chainId, core, strategy.depositTokens])

  const borrowTokens = useMemo(() => {
    return strategy.borrowTokens.map(d => core.tokens(d, chainId))
  }, [chainId, core, strategy.borrowTokens])

  const rewardToken = useMemo(() => {
    return core.tokens(strategy.rewardToken, chainId)
  }, [chainId, core, strategy.rewardToken])

  return (
    <Card className={'material-secondary'}>
      <ConfirmCloseModal
        open={confirmCloseModal}
        onClose={() => setConfirmCloseModal(false)}
        trove={trove}
        strategy={strategy}
      />
      <PlatformIcon>
        <IconLoader iconName={strategy.platform} iconType={'exchangePlatform'} width={32} />
      </PlatformIcon>
      <div className={'m-b-24'}>
        <div className={'single-line-center-center m-b-12'}>
          <ImgCon>
            <IconLoader
              iconName={props.strategy.trove}
              iconType={'tokenSymbol'}
              height={98}
              width={98}
            />
          </ImgCon>
        </div>
        <TextWrapper
          text={props.strategy.trove}
          fontSize={24}
          fontFamily={'Syne'}
          align={'center'}
          fontWeight={600}
        />
      </div>
      <DataField
        label={'Debt'}
        isValueLoading={trove.isLoading}
        value={`${Number(
          getDisplayBalance(trove.debt, 18, 3),
        ).toLocaleString()} ${'ARTH'}`}
        labelFontColor={theme.color.transparent[100]}
        labelFontWeight={'bold'}
        className={'m-b-16'}
      />
      <div className={'single-line-start-between m-b-16'}>
        <TextWrapper
          text={'Principal Collateral'}
          fontWeight={600}
          Fcolor={theme.color.transparent[100]}
        />
        <div>
          {depositTokens.map((item) => {
            return (
              <TextWrapper
                text={`${getDisplayBalance(principalAmount, item.decimal, 3)} ${item.symbol}`}
                className={'m-b-4'}
                align={'right'}
              />
            )
          })}
        </div>
      </div>
      <div className={'single-line-start-between m-b-16'}>
        <TextWrapper
          text={'Leverage'}
          fontWeight={600}
          Fcolor={theme.color.transparent[100]}
        />
        <div>
          <TextWrapper
            text={`${(leverage / 1000).toFixed(2)}x`}
            className={'m-b-4'}
            align={'right'}
          />
        </div>
      </div>
      <div className={'single-line-start-between m-b-16'}>
        <TextWrapper
          text={'Exposure'}
          fontWeight={600}
          Fcolor={theme.color.transparent[100]}
        />
        <div>
          {borrowTokens.map((item, index) => {
            return (
              <TextWrapper
                text={`${getDisplayBalance(positions.value[index], item.decimal, 3)} ${item.symbol}`}
                className={'m-b-4'}
                align={'right'}
              />
            )
          })}
        </div>
      </div>
      <DataField
        label={'Your Collateral Ratio'}
        value={`${Number(trove.collateralRatio.toNumber()).toLocaleString()}%`}
        labelFontColor={theme.color.transparent[100]}
        valueFontColor={theme.color.green[300]}
        labelFontWeight={'bold'}
        className={'m-b-16'}
      />
      <DataField
        label={'Total Collateral Ratio'}
        value={`${Numeral(getDisplayBalance(totalCollteralRatio.value, 18, 6)).format(
          '0,0.000%',
        )}`}
        labelFontColor={theme.color.transparent[100]}
        labelFontWeight={'bold'}
        className={'m-b-16'}
      />
      <DataField
        label={'Reward'}
        isValueLoading={rewards.isLoading}
        value={`${Number(getDisplayBalance(rewards.value, 18, 3))} ${strategy.rewardToken}`}
        labelFontColor={theme.color.transparent[100]}
        valueFontColor={theme.color.primary[300]}
        labelFontWeight={'bold'}
        className={'m-b-16'}
      />
      <div className={'m-t-32 single-line-center-center'}>
        <TextButton
          onClick={() => setConfirmCloseModal(true)}
          text={'Close Position & Claim Rewards'}
          tracking_id="close_loan"
          tracking_params={{
            collateral: strategy.trove,
            action: 'initial',
            amount: Number(getDisplayBalance(trove.coll, 18, 3)),
            other: {
              debt: Number(getDisplayBalance(trove.debt, 18, 3)),
            },
          }}
        />
      </div>
    </Card>
  );
};

export default MobilePositionCard;

const Card = styled.div`
  padding: 32px;
  border-radius: 12px;
  height: 100%;
  position: relative;
  @media(max-width: 600px) {
    padding: 32px 24px;
  }
`;

const ImgCon = styled.div`
  //width: 98px;
  //height: 98px;
  //box-shadow: 2px 2px 20px #2A2827;
  //border-radius: 50%;
  position: relative;
`;

const PlatformIcon = styled.div`
  position: absolute;
  top: -16px;
  left: 50%;
  transform: translate(-50%, 0);
`
