import React, { useEffect, useState } from "react";

import PageHeader from '../../theme/components/PageHeader';
import YourPosition from '../YourPosition';

import PoolTable from './components/PoolTable';
import { useParams } from "react-router-dom";
import Toggler from "./components/Toggler";
import TextButton from "../../theme/components/TextButton";
import { useMediaQuery } from "react-responsive";
import TextWrapper from "../../theme/components/TextWrapper";
import theme from "../../theme";
import IconLoader from "../../theme/components/IconLoader";
import { VfatLink } from "../Farming";
import StakedBanner from "../../components/StakedBanner";

type IMode = 'your-position' | 'all-strategies';

const Strategies = () => {
  const { mode } = useParams();
  const [selectedMode, setSelectedMode] = useState<IMode>('all-strategies');
  const isMobile = useMediaQuery({ maxWidth: '600px' });

  useEffect(() => {
    if (mode === 'your-position' || mode === 'all-strategies') {
      window.location.href = `/#/strategies/${mode}`;
      setSelectedMode(mode)
    } else {
      window.location.href = `/#/strategies/your-position`;
      setSelectedMode('all-strategies');
      /*if (collateralForActiveTroves.value.length > 0) {
        window.location.href = `/#/loans/your-loans`;
        setSelectedMode('your-loans')
      } else {
        window.location.href = `/#/loans/all-loans`;
        setSelectedMode('all-loans')
      }*/
    }
  }, [mode]);

  return (
    <div className={'custom-container'}>
      <VfatLink onClick={() => window.location.href = '/#/farming'}>
        <TextWrapper
          text={'New'}
          Fcolor={theme.color.primary[300]}
          fontWeight={600}
          className={'m-r-12'}
        />
        <TextWrapper text={'Farm $ARTH at more than 100% APR'} className={'m-r-12'} />
        <IconLoader iconName={'ArrowTailRight'} iconType={'arrow'} />
      </VfatLink>
      <PageHeader
        title={'Strategies'}
        subTitle={`Leverage strategies allow you to gain more exposure to assets/farms/LPs using ARTH's minting mechanism`}
        showLearnMore={true}
        learnMoreLink={'https://docs.arthcoin.com/tutorials/leverage-strategies'}
      />
      <StakedBanner />
      <div className={'single-line-center-between m-b-24'}>
        <Toggler mode={selectedMode} onModeChange={(data) => window.location.href = `/#/strategies/${data}`} />
        {!isMobile && <TextButton
          onClick={() =>
            window.open(
              "https://docs.google.com/forms/d/e/1FAIpQLSdOoo2_88RuRZoDfOEj8B_fN7r8DUmXh9mZH5Mkj59WLzrQ6Q/viewform?usp=sf_link"
            )
          }
          text={"Request Pool"}
          className={"m-l-16"}
        />}
      </div>
      {/* <div className='m-b-40'>
        <DataPointsInfo />
      </div> */}
      {/* <div className={'mo-custom-container '}>
        <TextWrapper
          text={'STRATEGIES'}
          fontFamily={'Syne'}
          fontSize={24}
          FletterSpacing={'0.1rem'}
          fontWeight={'bold'}
          className={'m-b-32'}
        />
      </div> */}
      {selectedMode === 'all-strategies' && <PoolTable />}
      {selectedMode === 'your-position' && <YourPosition />}
    </div>
  );
};

export default Strategies;
