import React, { useEffect, useMemo, useState } from "react";
import styled from 'styled-components';

import { useGetActiveChainId } from '../../../state/chains/hooks';
import theme from '../../../theme';
import DataField from '../../../theme/components/DataField';
import IconLoader from '../../../theme/components/IconLoader';
import TextWrapper from '../../../theme/components/TextWrapper';
import { troveDetails } from '../../../utils/tokenDetails';
import { StrategiesStaticInfo } from "../../../protocol/config";
import TextButton from "../../../theme/components/TextButton";
import isStrategyOpen from "../../../hooks/state/strategies/isStrategyOpen";
import Loader from "react-spinners/BeatLoader";
import { formatToBN } from "../../../utils/formatBalance";
import useGetAPRValue from "../../../hooks/state/strategies/useGetAPRValue";
import Button from "../../../theme/components/Button";
import useCore from "../../../hooks/useCore";
import { useWallet } from "use-wallet";
import InfoTip from "../../../theme/components/InfoTip";

interface IProps {
  strategy: StrategiesStaticInfo;
  onTroveClick: () => void;
}

const MobilePoolCard = (props: IProps) => {
  const { strategy, onTroveClick } = props;
  const chainId = useGetActiveChainId();
  const core = useCore();
  const { account } = useWallet();

  const troveDetail = troveDetails(strategy.trove, chainId);
  const isUserWhiteListed = core.isUserWhiteListed(chainId, account);

  const hasPosition = isStrategyOpen(strategy.trove);
  const aprValues = useGetAPRValue(strategy.trove);



  const maxLeverage = useMemo(() => {
    if (aprValues.isLoading) {
      return {
        isLoading: true,
        value: '-'
      }
    } else {
      if (aprValues.value.totalAPR === '-' || !Number(aprValues.value.totalAPR)) {
        return {
          isLoading: false,
          value: '-'
        }
      } else {
        return {
          isLoading: false,
          value: Number(aprValues.value.totalAPR) * strategy.maxLeverage
        }
      }
    }
  }, [aprValues.isLoading, aprValues.value.totalAPR, strategy.maxLeverage])

  return (
    <Card className={'material-secondary pointer'}>
      <PlatformIcon>
        <IconLoader iconName={strategy.platform} iconType={'exchangePlatform'} width={32} />
      </PlatformIcon>
      <div className={'m-b-24'}>
        <div className={'single-line-center-center m-b-12'}>
          <IconLoader
            iconName={troveDetail.depositToken}
            iconType={'tokenSymbol'}
            width={98}
          />
        </div>
        <TextWrapper
          text={strategy.displayName}
          fontSize={24}
          fontFamily={'Syne'}
          align={'center'}
          fontWeight={600}
        />
      </div>
      <div className={'single-line-center-center m-b-24'}>
        <TextWrapper
          text={strategy.description}
          align={'center'}
          Fcolor={theme.color.transparent[100]}
        />
      </div>
      <div className={'m-b-32'}>
        <DataField
          label={'Leveraged APR'}
          value={`${Number(maxLeverage.value)?.toFixed(3) || '-'}%`}
          isValueLoading={maxLeverage.isLoading}
          labelFontColor={theme.color.transparent[100]}
          valueFontColor={theme.color.primary[300]}
          valueFontSize={16}
          valueFontWeight={'bold'}
          labelFontWeight={'bold'}
          className={'m-b-12'}
        />
        <DataField
          label={'APR'}
          value={`${Number(aprValues.value.totalAPR)?.toFixed(3) || '-'}%`}
          isValueLoading={aprValues.isLoading}
          labelFontColor={theme.color.transparent[100]}
          labelFontWeight={'bold'}
          className={'m-b-12'}
        />
        <DataField
          label={'MAHA APR'}
          // value={`${Number(aprValues.value.totalAPR)?.toFixed(3) || '-' }%`}
          value={'0%'}
          isValueLoading={aprValues.isLoading}
          labelFontColor={theme.color.transparent[100]}
          labelFontWeight={'bold'}
          className={'m-b-12'}
        />
        <DataField
          label={'Max Leverage'}
          value={`${strategy.maxLeverage}x`}
          labelFontColor={theme.color.transparent[100]}
          labelFontWeight={'bold'}
          className={'m-b-12'}
        />
        <DataField
          label={'One-time fee'}
          value={'0.00%'}
          labelFontColor={theme.color.transparent[100]}
          labelFontWeight={'bold'}
          className={'m-b-12'}
        />
        <DataField
          label={'Reward Fee'}
          value={'0.00'}
          labelFontColor={theme.color.transparent[100]}
          labelFontWeight={'bold'}
        />
      </div>
      {troveDetail.isDeprecated && <InfoTip type={"Error"} msg={"This Strategy is depreciated"} className={'m-b-8'} />}
      {hasPosition.isLoading
        ? <div className={'single-line-center-center'}>
          <Loader loading={hasPosition.isLoading} size={6} color={'white'} />
        </div>
        : hasPosition.value
          ? <TextButton
            text={"View Position"}
            onClick={() => window.location.href = '/#/strategies/your-position'}
            align={"center"}
            tracking_id={'view_position'}
            tracking_params={{

            }}
            className={'p-t-12 p-b-12'}
          />
          : troveDetail.isDeprecated
            ? <Button
              text={"Open Position (Depreciated)"}
              onClick={onTroveClick}
              disabled={true}
            />
            : <Button
              text={"Open Position"}
              onClick={onTroveClick}
              disabled={troveDetail.isDeprecated}
            />
      }
    </Card>
  );
};

export default MobilePoolCard;

const Card = styled.div`
  padding: 32px;
  border-radius: 12px;
  height: 100%;
  cursor: pointer;
  position: relative;
  @media (max-width: 600px) {
    padding: 24px;
  }
`;

const PlatformIcon = styled.div`
  position: absolute;
  top: -16px;
  left: 50%;
  transform: translate(-50%, 0);
`
