import React from 'react';

import DataPointsInfo from './components/DataPointsInfo';
import PositionTable from './components/PositionTable';
import MobilePositionCard from "./components/MobilePositionCard";
import { useGetActiveChainId } from "../../state/chains/hooks";
import useCore from "../../hooks/useCore";
import { useMediaQuery } from "react-responsive";
import useGetLeverageTrove from "../../hooks/state/troveManager/useGetLeverageTrove";
import LoadingPage from "../../components/LoadingPage";
import TextWrapper from "../../theme/components/TextWrapper";
import { Grid } from "@material-ui/core";
import { useWallet } from "use-wallet";

const YourPosition = () => {
  const activeChainId = useGetActiveChainId();
  const core = useCore();
  const {account} = useWallet();
  const strategies = core.getAllStrategies(activeChainId);
  const isMobile = useMediaQuery({ maxWidth: '600px' });

  const trove = useGetLeverageTrove(false);

  if (trove.isLoading) {
    return (
      <div style={{ position: 'relative', height: '150px' }}>
        <LoadingPage />
      </div>
    );
  }

  if (trove.value.length <= 0) {
    return (
      <div className="m-t-40 m-b-40 material-primary">
        <TextWrapper
          text={'You dont have any positions open'}
          fontSize={24}
          fontWeight={'bold'}
          className="m-b-8"
          align="center"
        />
        <TextWrapper
          text={'Open a leveraged position on a farm by interacting with one of the strategies'}
          fontSize={18}
          className="m-b-8"
          Fcolor={'#FFFFFF64'}
          align={'center'}
        />
      </div>
    );
  }

  return (
    <div className={'mo-custom-container'}>
      <Grid container spacing={2}>
        {trove.value.map((trove) => {
          return (
            <Grid item lg={4} md={6} sm={12} xs={12}>
              <MobilePositionCard
                strategy={strategies.find(s => s.trove === trove.token)}
                trove={trove}
                key={trove.token}
              />
            </Grid>
          );
        })}
      </Grid>
    </div>
  );
};

export default YourPosition;
