import { BigNumber } from '@ethersproject/bignumber';
import { useCallback, useEffect, useMemo, useState } from 'react';
import { useWallet } from 'use-wallet';

import { FarmingConfigurations, StrategiesStaticInfo } from '../../../protocol/config';
import { useBlockNumber } from '../../../state/application/hooks';
import { useGetActiveChainId } from '../../../state/chains/hooks';
import useCore from '../../useCore';

type State = {
  isLoading: boolean;
  value: BigNumber;
};

export default (strategy: StrategiesStaticInfo) => {
  const [value, setValue] = useState<State>({ isLoading: true, value: BigNumber.from(0) });

  const core = useCore();
  const { account } = useWallet();
  const chainId = useGetActiveChainId();

  const fetchValue = useCallback(async () => {
    if (!account) {
      setValue({ isLoading: false, value: BigNumber.from(0) });
      return;
    }

    const contract = core.contracts(strategy.contract, chainId);
    const balance: BigNumber = await contract.rewardsEarned(account);

    setValue({ isLoading: false, value: balance });
  }, [account, core, strategy.contract, chainId]);

  useEffect(() => {
    if (core.isUnlocked) {
      fetchValue().catch((err) => {
        setValue({ isLoading: false, value: BigNumber.from(0) });
        console.error(`Failed to fetch strategy earnings: ${err}`)
      });
    }
  }, [core.isUnlocked, account, fetchValue]);

  return value;
};
