import React, { useMemo, useState } from 'react';
import { useParams } from 'react-router-dom';

import Grid from '@material-ui/core/Grid';

import Container from '../../components/Container';
import useCore from '../../hooks/useCore';
import { useGetActiveChainId } from '../../state/chains/hooks';
import theme from '../../theme';
import IconLoader from '../../theme/components/IconLoader';
import PageHeader from '../../theme/components/PageHeader';
import TextWrapper from '../../theme/components/TextWrapper';
import LoanInfo from '../LoanInfo/LoanInfo';

import CreatePosition from './components/CreatePosition';
import LeverageInfo from "./components/LeverageInfo";
import isStrategyOpen from "../../hooks/state/strategies/isStrategyOpen";
import LoadingPage from "../../components/LoadingPage";
import { useWallet } from "use-wallet";

const AddStrategies = () => {
  // @ts-ignore
  const { id } = useParams();
  const activeChainId = useGetActiveChainId();
  const {account} = useWallet();

  const core = useCore();
  const strategies = core.getAllStrategies(activeChainId, account);
  const strategy = useMemo(() => strategies.find((s) => s.trove === id), [strategies, id]);

  if (!strategy) {
    window.location.href = '/#/strategies';
    return <div />;
  }

  const hasPosition = isStrategyOpen(strategy.trove);

  if (hasPosition.isLoading) {
    return (
      <div style={{ position: 'relative', height: '150px' }}>
        <LoadingPage />
      </div>
    )
  }

  if (hasPosition.value) {
    window.location.href = '/#/strategies/your-position';
    return <div />;
  }

  return (
    <div className="custom-container">
      <div className="m-b-40 mo-custom-container">
        <div className={'m-b-20 single-line-center-start'}>
          <a href={'/#/strategies/overview'}>
            <TextWrapper text={'Dashboard'} Fcolor={theme.color.transparent[200]} />
          </a>
          <IconLoader iconName={'ArrowRight'} iconType={'arrow'} className={'m-r-4 m-l-4'} />
          <TextWrapper text={'New Loan'} Fcolor={theme.color.transparent[100]} />
        </div>
        <PageHeader
          title={'Execute a strategy'}
          subTitle={"Decide the parameters on how much leverage you would like to take."}
        />
      </div>
      <Grid container className="m-t-24" spacing={2}>
        <Grid item lg={6} md={6} sm={12} xs={12}>
          <CreatePosition strategy={strategy} />
        </Grid>
         <Grid item lg={6} md={6} sm={12} xs={12}>
          <LeverageInfo strategy={strategy}/>
        </Grid>
      </Grid>
    </div>
  );
};

export default AddStrategies;
